/* eslint-disable no-console */
import React, { useContext, useEffect, useState } from "react";
import { Menu as UikitMenu, ConnectorId } from "@plasmaswap-libs/uikit";
import { useWeb3React } from "@web3-react/core";
import { allLanguages } from "constants/localisation/languageCodes";
import { LanguageContext } from "hooks/LanguageContext";
import useTheme from "hooks/useTheme";
import { injected, bsc, walletconnect } from "connectors";
import { useMasterchef } from "hooks/useContract";
import BigNumber from "bignumber.js";
// import farmsConfig from "constants/farms";
// import fetchFarms from "./fetchFarms";
import links from "./config";

const Menu: React.FC = (props) => {
  const { account, activate, deactivate } = useWeb3React();
  const { selectedLanguage, setSelectedLanguage } = useContext(LanguageContext);
  const { isDark, toggleTheme } = useTheme();

  const masterChefContract = useMasterchef();
  const [data, setData] = useState({
      plasmaPriceUsd: 0
  });

  useEffect(() => {
    async function fetchData() {
      // const farms = await fetchFarms(farmsConfig);
      // const bnbBusdPrice = new BigNumber(1).div(farms[0].tokenPriceVsQuote);
      // TODO: UPDATE farms[1] AFTER THE PLASMA-BNB FARM IS ADDED, AND REMOVE .div()
      // const plasmaPriceUsd = bnbBusdPrice.times(farms[0].tokenPriceVsQuote).div(100).toNumber();

      const bnbBusdPrice = new BigNumber(1);
      const plasmaPriceUsd = bnbBusdPrice.toNumber();

      setData({ plasmaPriceUsd });
    }

    fetchData();
  }, [masterChefContract]);

  return (
    <UikitMenu
      links={links}
      account={account as string}
      login={(connectorId: ConnectorId) => {
        if (connectorId === "walletconnect") {
          return activate(walletconnect)
        }

        if (connectorId === "bsc") {
          return activate(bsc)
        }

        return activate(injected)
      }}
      logout={deactivate}
      isDark={isDark}
      toggleTheme={toggleTheme}
      currentLang={selectedLanguage?.code || ""}
      langs={allLanguages}
      setLang={setSelectedLanguage}
      cakePriceUsd={data.plasmaPriceUsd}
      {...props}
    />
  );
};

export default Menu;
